.gpt3__header {
  display: flex;
  flex-direction: row;
}
.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin-right: 5rem;
}
.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 52px;
  line-height: 75px;
  letter-spacing: -0.04em;

  /* border: 1px solid #000000; */
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.gpt3__header-content > p {
  color: var(--color-text);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;

  margin-top: 1.5rem;
}
.gpt3__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  /* IMPORTANT set to flex make it by default a row  */
  display: flex;
  flex-direction: row;
}
.gpt3__header-content__input input {
  flex: 2;

  width: 100%;
  min-height: 50px;

  font-family: var(--font-family);

  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
  padding: 0 1rem;

  outline: none;
  color: #ffffff;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.gpt3__header-content__input button {
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  outline: none;

  background: #ff4820;
  border-radius: 0px 5px 5px 0px;
  border: 2px solid #ff4820;

  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 400;
  line-height: 25px;

  color: #ffffff;
  cursor: pointer;

  outline: none;

  padding: 0 1rem;
}
.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;

  margin-top: 2rem;
}
.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}
.gpt3__header-content__people p {
  margin: 0 0 0 1rem;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #fff;
  text-align: center;
}
.gpt3__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt3__header-image img {
  /* width: 100%;
  height: 100%; */
  /* object-fit: cover; */
  max-width: 100%;
  height: auto;
}

@media screen and (max-width: 1100px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header-content h1 {
    font-size: 48px;
    line-height: 60px;
  }

  .gpt3__header-content p {
    font-size: 16px;
    line-height: 24px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people p {
    margin: 0;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header-content h1 {
    font-size: 36px;
    line-height: 48px;
  }

  .gpt3__header-content p {
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}
